.header {
  z-index: 1;
  width: 100%;
  background-color: #fff;
  position: fixed;
  top: 0;
  box-shadow: 0 5px 10px -6px #0000001a;
}

.header .container {
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  display: flex;
}

.header-logo {
  letter-spacing: .05em;
  margin-right: 20px;
  font-size: 20px;
  font-weight: bold;
  line-height: 1.2;
}

.header-logo a {
  color: #333;
}

.gnav-list {
  justify-content: space-between;
  list-style: none;
  display: flex;
}

.gnav-item:not(:last-child) {
  margin-right: 20px;
}

.gnav-item a {
  letter-spacing: .05em;
  color: #333;
  padding: 5px 0;
  font-size: 13px;
  font-weight: bold;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.gnav-item a:after {
  width: 0;
  height: 3px;
  content: "";
  background-color: #333;
  transition: all .3s;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.gnav-item a:hover:after {
  width: 100%;
}

@media screen and (max-width: 767px) {
  .header .container {
    padding: 15px;
  }

  .header-logo {
    margin-right: 15px;
    font-size: 15px;
  }

  .gnav-item:not(:last-child) {
    margin-right: 10px;
  }

  .gnav-item a {
    font-size: 10px;
  }

  .gnav-item a:after {
    display: none;
  }
}

.footer {
  background-color: #333;
  padding: 30px;
}

.copyright {
  text-align: center;
  color: #797979;
  font-size: 10px;
}

@media screen and (max-width: 767px) {
  .footer {
    padding: 20px;
  }
}

